import React from "react";

export default function ConfigCreate() {
  return (
    <div>
      <input type="text" placeholder="CSS Name" required={true} />
      <input type="text" placeholder="Logo Image Url" required={false} />
      <input type="text" placeholder="Cover Image Url" required={false} />
      <input type="text" placeholder="Description" required={false} />
      <input type="text" placeholder="Github Repo Link" required={false} />
      <input type="text" placeholder="Website Link" required={false} />
      <p>Credits</p>
      <input type="text" placeholder="Credit name" required={true} />
      <p>Contacts</p>
      <input type="text" placeholder="Type of reference" required={true} />
      <input type="text" placeholder="Reference" required={true} />

      <p>Main</p>
      <p>Target type</p>
      <input id="zip" type="radio" name="target_type" value="zip" />
      <label for="zip">Zip</label>
      <input
        id="css"
        type="radio"
        name="target_type"
        value="css"
        checked={true}
      />
      <label for="css">CSS</label>

      <input type="text" placeholder="Target URL" required={true} />
      <input type="text" placeholder="Target File" required={false} />
    </div>
  );
}
